@import '../../../style/vars.scss';

.search {
  max-width: 450px;
  min-width: 250px;
  display: flex;
  width: 100%;
  height: 40px;
  margin: 10px;
  background: #444;
  background: rgba(0, 0, 0, 0.2);
  border-radius: 3px;
  border: 1px solid rgb(166, 166, 166);
  
  input {
    width: 370px;
    padding: 10px 5px;
    float: left;
    color: #fff;
    border: 0;
    background: transparent;
    border-radius: 3px 0 0 3px;
    @extend %os-light;
    font-size: 17px;
    letter-spacing: 1.5px;

    &:focus {
      outline: 0;
      background: transparent;
    }

    &:focus::placeholder {
      color: transparent;
    }
  }

  button {
    position: relative;
    float: right;
    border: 0;
    padding: 0 30px;
    cursor: pointer;
    height: 40px;
    max-width: 120px;
    color: #fff;
    font-size: 17px;
    background: transparent;
    border-left: 1px solid #fff;
    border-radius: 0 3px 3px 0;
    transition: all 0.3s;

    &:hover {
      background: #fff;
      color: #444;
    }

    &:active {
      box-shadow: 0px 0px 12px 0px rgba(225, 225, 225, 1);
    }

    &:focus {
      outline: 0;
    }
  }
}
